<template>
  <div>
    <el-form class="account-plat-formitem" label-position="left" :model="ruleForm" ref="ruleForm" label-width="130px"
             style="text-align: left;font-size:20px;font-weight:
      bold;" @submit.native.prevent>
      <el-card class="account-plat-card1 account-plat-card2" shadow="always" :body-style="cardBodyStyle" style="">
        <div style="margin-top: 10px">
          <el-row :gutter="24">
            <el-col :md="24">
              <el-form-item label="选择设备:" prop="di_uuid">
                <el-select placeholder="请选择设备" v-model="ruleForm.di_uuid" style="width: 300px"
                           @change="equipment_Selection"
                >
                  <el-option
                    v-for="item in tableData_equipment"
                    :key="item.di_uuid"
                    :label="item.Id"
                    :value="item.di_uuid"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
        </div>
      </el-card>
    </el-form>
    <el-card class="account-plat-card1 account-plat-card2" shadow="always" :body-style="cardBodyStyle">
      <div v-if="this.tableDataequipment == 0" style="color: red">请先创建设备</div>
      <div style="display:flex;" class="row" v-if="this.tableDataequipment == 1">
        <div style="margin: 30px;" v-for="(item,index) in img_tableData"
             :key="index" v-if="item.bdbi_path !== undefined">
          <div style="height: 250px;width: 300px;color: #4b86bc;" class="item">
            <el-image style="height: 150px;width: 300px;" :src="item.bdbi_path"/>
            <div style="border: 1px solid #000;width: 300px;">
              <div style="font-size: 14px;display: flex;justify-content: space-between">
                <div v-if="item.bdbi_type === '1'">素材类型：图片</div>
                <div>播放时长：15秒</div>
              </div>
              <div style="font-size: 14px;">
                <div>发布人：{{ item.cui_realname }}</div>
              </div>
              <div style="font-size: 14px;">
                <div>发布日期：{{ item.bdbi_writetime_new }}</div>
              </div>
              <div>
                <div
                  style="cursor: pointer; font-size: 16px;width: 100px;background: #3a8ee6;color: #fff;margin: 10px auto;border-radius: 5px"
                  @click="delete_img(item)">删除
                </div>
              </div>
            </div>
          </div>
        </div>
        <div style="margin: 30px" v-if="img_tableData.length < 9">
          <div style="cursor: pointer; height: 200px;width: 200px;color: #4b86bc;border: 1px solid #000" @click="img_button()">
            <div style="padding-top: 30px;font-size: 80px;line-height: 80px">+</div>
            <div style="font-size: 24px">新增广告</div>
          </div>
        </div>
      </div>
    </el-card>

    <!--弹窗-->
    <el-dialog :visible.sync="img_dialog"
               :close-on-press-escape="false"
               :close-on-click-modal="false"
               :show-close="false"
    >
      <div slot="title" style="text-align: left;font-size:20px;font-weight: bold;">
        <span>{{ this.tableDialgTitle }}</span>
      </div>
      <div style="margin-left: 100px">
        <div style="display: flex">
          <div>上传广告：</div>
          <div style="position: relative;margin-left: 30px">
            <el-image :src="this.banner_img" style="height: 200px;width: 350px;"></el-image>
            <el-button @click="onOpen()" style="position: absolute;top: 80px;left: 130px">上传图片</el-button>
            <div>为保证设备端显示效果，图片分辨率建议1920*1080（像素）</div>
            <div>图片格式：支持jpg、png、jpeg等常见格式</div>
          </div>
        </div>
        <div style="display: flex;margin-top: 30px">
          <div>上传状态：</div>
          <div style="position: relative;margin-left: 30px">
            <div v-if="this.banner_img === ''">待上传</div>
            <div v-if="this.banner_img !== ''" style="color: #5daf34">上传成功</div>
          </div>
        </div>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="img_cancel()">取 消</el-button>
        <el-button type="primary" @click="img_submit()">确 定</el-button>
      </div>
    </el-dialog>

    <!--截图功能-->
    <el-dialog :title="options.title" :visible.sync="dialogVisible" :before-close="handleClose">
      <div class="el-cropper">
        <div>
          <div style="margin: 10px 0">
            <div style="font-weight: 600">操作说明：</div>
            <div>通过鼠标滚轮调整图片大小</div>
            <div>通过鼠标左键调整裁剪框位置</div>
          </div>
          <div class="cropper-content">
            <div class="cropper">
              <vueCropper ref="cropper"
                          :img="options.img"
                          :outputSize="options.outputSize"
                          :outputType="options.outputType"
                          :full="options.full"
                          :canMove="options.canMove"
                          :canMoveBox="options.canMoveBox"
                          :original="options.original"
                          :autoCrop="options.autoCrop"
                          :autoCropWidth="options.autoCropWidth"
                          :autoCropHeight="options.autoCropHeight"
                          :fixedBox="options.fixedBox"
                          :enlarge="options.enlarge"
                          :centerBox="options.centerBox"
                          :fixedNumber="options.fixedNumber"
                          :fixed="options.fixed"
                          :maxImgSize="options.maxImgSize"
                          :mode="options.mode"
                          :infoTrue="options.infoTrue"
                          :high="options.high"
                          @realTime="realTime"
              ></vueCropper>
            </div>
            <div class="show-preview" :style="{'overflow': 'hidden'}" v-show="false">
              <div class="wait">预览区域：</div>
              <div class="preview">
                <img :src="imgsrc" alt="" :style="scaleFun">
              </div>
              <!--              <div class="detailsclass">-->
              <!--                <p class="">截图详情：</p>-->
              <!--                <p v-text="options.details" :title="options.details"></p>-->
              <!--              </div>-->
            </div>
          </div>
        </div>
        <div style="  margin-top: 10px;">
          <el-row>
            <!-- 选择图片 -->
            <el-col :span="6">
              <div>
                <label class="el-button el-button--default" for="uploads1">选择图片
                  <input type="file" id="uploads1"  ref="reference" style="position:absolute; clip:rect(0 0 0 0);"
                         accept="image/png, image/jpeg, image/jpg" @change="uploadImg($event, 1)"/>
                </label>
              </div>
            </el-col>
            <!-- 功能按钮 -->
            <el-col :span="6">
              <div>
                <!--                <el-button type="ghost" @click="changeScale(1)" icon="plus-round" class="l-btn">+</el-button>-->
                <!--                <el-button type="ghost" @click="changeScale(-1)" icon="minus-round" class="l-btn">-</el-button>-->
                <el-button @click="rotateLeft"><span style="font-weight: 600;">↺</span></el-button>
                <el-button @click="rotateRight"><span style="font-weight: 600;">↻</span></el-button>
              </div>
            </el-col>
            <el-col :span="12">
              <div>
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button @click="oncropped('base64')">确 定</el-button>
              </div>
            </el-col>
          </el-row>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {VueCropper} from 'vue-cropper';
import axios from "axios";

export default {
  name: "equipment_picture",
  components: {
    VueCropper,
  },
  data() {
    return {
      tableDataequipment:'2',
      bdbi_id: '',
      img_tableData: [],
      banner_img: '',
      img_dialog: false,
      tableDialgTitle: '',
      ruleForm: {
        di_uuid: '',
      },
      cardBodyStyle: 'padding-top:10px;padding-bottom:10px;',
      tableData_equipment: [],//设备数据
      //截图功能
      options: {
        img: "",
        outputSize: 1,//	 裁剪生成图片的质量 0.1 - 1
        outputType: 'png | jpeg | jpg',	//裁剪生成图片的格式 jpeg || png || webp
        full: true,//是否输出原图比例的截图,true是不失真
        canMove: true,//图片是否可以移动
        canMoveBox: true,//	截图框能否拖动
        original: false,//图片按照原始比例渲染,true会超出屏幕区域，false是适应屏幕区域
        autoCrop: true,//是否默认生成截图框
        autoCropWidth: 400,
        autoCropHeight: 200,
        fixedBox: true,//固定截图框大小 不允许改变
        enlarge: 1,
        centerBox: true,//截图框是否被限制在图片里面
        fixed: true,//	是否开启截图框宽高固定比例
        fixedNumber: [1.77, 1],// 截图框的宽高比例 [宽度, 高度]
        maxImgSize: 2000,
        mode: 'contain',//整个图片填充裁剪框 contain , cover, 100px, 100% auto
        high: true,
        infoTrue: true,//true截图框展示真实图片的宽高,false截图框内显示可缩放图片

        title: '图像裁剪',
        details: '选择图片，上传',
      },
      dialogVisible: false,
      imgsrc: '',
    }
  },
  created() {
    this.select_equipment();
    console.log('我的：'+this.customGlobal.serverUrl.orgBaseURI)
  },
  // 预览实时缩放
  computed: {
    scaleFun: function () {
      var scale = this.scaleratioXY;
      return `transform:scale(${scale},${scale})`;
    }
  },
  methods: {
    handleClose(done) {
      this.$confirm('确认关闭？')
        .then(_ => {
          done();
          this.onclose();
        })
        .catch(_ => {
        });
    },
    // 预览
    realTime(data) {
      var that = this;
      var dataoption = data;
      this.$refs.cropper.getCropData(data => {
        that.imgsrc = data;
        if (dataoption.w >= 200 || dataoption.h >= 200) {
          if (dataoption.w >= dataoption.h) {
            that.scaleratioXY = 200 / dataoption.w;
          }
          if (dataoption.h > dataoption.w) {
            that.scaleratioXY = 200 / dataoption.h;
          }
        }
      });
    },
    // 选择要上传的图片
    uploadImg(e, num) {
      // 选择要上传的图片
      // this.option.img
      var that = this;
      var file = e.target.files[0];


      if (!/\.(jpg|jpeg|png|JPG|PNG)$/.test(e.target.value)) {
        alert('图片类型必须是.gif,jpeg,jpg,png中的一种');
        return false;
      }
      var reader = new FileReader();
      reader.onload = e => {
        let data;
        if (typeof e.target.result === 'object') {
          // 把Array Buffer转化为blob 如果是base64不需要
          data = window.URL.createObjectURL(new Blob([e.target.result]));
        } else {
          data = e.target.result;
        }
        if (num === 1) {
          that.options.img = data;
          // var dat = {img: data};
          // that.$emit('updateimg', dat);
        } else if (num === 2) {
          this.example2.img = data;
        }
      };
      // 转化为base64
      reader.readAsDataURL(file);
      // 转化为blob
      // reader.readAsArrayBuffer(file)
    },
    // 图片缩放
    changeScale(num) {
      num = num || 1;
      this.$refs.cropper.changeScale(num);
    },
    // 左旋转
    rotateLeft() {
      this.$refs.cropper.rotateLeft();
    },
    // 右旋转
    rotateRight() {
      this.$refs.cropper.rotateRight();
    },
    // 裁剪
    oncropped(type) {
      this.$refs.reference.value=''
      var that = this;
      if (type === 'blob') {
        this.$refs.cropper.getCropBlob(data => {
          // var img = window.URL.createObjectURL(data);
          // console.info("图片数据2："+data);
        });
      } else {
        this.$refs.cropper.getCropData(data => {
          var dats = {img: data};
          // console.info("图片数据1："+data);
          // that.$emit('getcropperimg', dats);
          if (this.options.img === '') {
            alert('您还没有选择图片');
          } else {
            //调用上传接口
            // this.onclose();
            this.uploadFileToServer(data);
            this.dialogVisible = false;
            this.options.img = '';
            this.imgsrc = '';
          }
        });
      }
    },
    //打开
    onOpen() {
      this.dialogVisible = true;
    },
    // base64转成bolb对象
    dataURItoBlob(base64Data) {
      let byteString;
      if (base64Data.split(',')[0].indexOf('base64') >= 0) {
        byteString = atob(base64Data.split(',')[1]);
      } else {
        byteString = unescape(base64Data.split(',')[1]);
      }
      let mimeString = base64Data.split(',')[0].split(':')[1].split(';')[0];
      let ia = new Uint8Array(byteString.length);
      for (let i = 0; i < byteString.length; i++) {
        ia[i] = byteString.charCodeAt(i);
      }
      return new Blob([ia], {type: mimeString});
    },
    async uploadFileToServer(data) {
      let img = new Image();
      img.src = data;
      img.onload = async () => {
        // let _data = this.compress(img);
        let blob = this.dataURItoBlob(data);
        let formData = new FormData();
        // formData.append('file', blob, this.options.fileInfo.name);
        formData.append('file', blob, this.customGlobal.getFormatDateTimeString("3") + ".png");
        this.uploadFileRequest(formData);
      };
    },
    async uploadFileRequest(params) {
      let that = this;
      const res = axios({
        method: 'post',
        url: this.customGlobal.serverUrl.orgBaseURI + "fr/postCallPowerFileUpload?userid=" + this.customGlobal.userInfoExt.cui_login_id + "&logic=2",
        data: params,
        headers: {
          'Content-Type': 'multipart/form-data'
        },
        onUploadProgress: function (progressEvent) {
          console.info("progressEvent:" + JSON.stringify(progressEvent));
          // 对原生进度事件的处理
        },

      }).then(function (response) {
        console.info("response:" + JSON.stringify(response));
        if (response.data.status === 'success') {
          console.info("上传成功：" + response.data.rawfilename);
          // let img = "http://192.168.3.62:38080/fr/imgserver/" + response.data.rawfilename;
          let img = that.customGlobal.serverUrl.orgBaseURI + "/fr/imgserver/" + response.data.rawfilename;
          // let img = "http://localhost:38080/fr/imgserver/" + response.data.rawfilename;
          console.info("图片：" + img);
          that.banner_img = img;
          console.info("图片：" + that.banner_img);
        } else {
          console.info("上传失败");
        }
      }).catch(function (error) {
        console.log(error);
      });
    },
    //下拉框点击事件
    equipment_Selection(key) {
      this.ruleForm.di_uuid = key;
      this.img_select();
    },
    //查询设备
    select_equipment() {
      let that = this;
      that.tableData_equipment = [];
      let _jsondata_ = {
        obj_bank_center_id: that.customGlobal.userInfoExt.cui_bank_center_id,
        obj_province_id: that.customGlobal.userInfoExt.cui_province_id,
        obj_city_id: that.customGlobal.userInfoExt.cui_city_id,
        obj_county_id: that.customGlobal.userInfoExt.cui_county_id,
        obj_station_id: that.customGlobal.userInfoExt.cui_station_id,
        perPageSize: 1000,
        currentPage: 1,
      };
      const loading = this.$loading({
        lock: true,
        text: '提交中...',
        target: '.main',
        spinner: 'el-icon-loading',

        background: 'rgba(0, 0, 0, 0)'
      });
      var a1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.Device_unique_ID_select,
        //this.customGlobal.serverUrl.baseURI,
        this.customGlobal.serverUrl.orgBaseURI,
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
      );
      a1.then((res) => {
        if (res.data.status === 'success') {
          loading.close();
          that.tableData_equipment = res.data.rows;
          if(that.tableData_equipment.length>0){
            that.tableDataequipment=1
          }else{
            that.tableDataequipment=0
          }
          that.tableData_equipment.forEach(val => {
            val.Id = val.di_uuid.substring(val.di_uuid.length-9,val.di_uuid.length)
          });
          if (res.data.rows.length !== 0){
              that.ruleForm.di_uuid = that.tableData_equipment[0].di_uuid;
              this.img_select();
          }
        } else {
          console.info("设备id查询失败");
          loading.close();
          if (res.data.msg === '请登录认证') {
            this.$router.push({path: '/login'});
          }
        }
      }, error => {
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(error.message), '设备id查询失败!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
        console.log("异步调用失败返回:" + JSON.stringify(res));
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(res.message), '设备id查询失败', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });
    },
    img_button() {
      this.banner_img = '';
      this.img_dialog = true;
      this.tableDialgTitle = '新增广告';
    },
    //提交
    img_submit() {
      let that = this;
      let _jsondata_ = {
        bdbi_device_id: that.ruleForm.di_uuid,
        bdbi_path: that.banner_img,
        bdbi_cui_id: that.customGlobal.userInfoExt.cui_uuid,
        bdbi_bank_center_id: that.customGlobal.userInfoExt.cui_bank_center_id,
        bdbi_province_id: that.customGlobal.userInfoExt.cui_province_id,
        bdbi_city_id: that.customGlobal.userInfoExt.cui_city_id,
        bdbi_county_id: that.customGlobal.userInfoExt.cui_county_id,
        bdbi_station_id: that.customGlobal.userInfoExt.cui_station_id,
        bdbi_duration: '',
        bdbi_caption: '',
        bdbi_describer: '',
        bdbi_type: '1',
        bdbi_status: '1',
        bdbi_order: '',
        bdbi_qr_type:''
      };
      const loading = this.$loading({
        lock: true,
        text: '提交中...',
        target: '.main',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0)'
      });
      var s3 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.Advertisement_add,
        //this.customGlobal.serverUrl.baseURI,
        this.customGlobal.serverUrl.orgBaseURI,
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
      );
      s3.then((res) => {
        if (res.data.status == 'success') {
          loading.close();
          this.$message({
            type: 'success',
            showClose: true,
            duration: 3000,
            dangerouslyUseHTMLString: true,
            message: '<span style="color:black;">图片上传成功</span>'
          });
          that.img_dialog = false;
          that.img_select();
        } else {
          console.info("图片上传失败");
          loading.close();
          if (res.data.msg === '请登录认证') {
            this.$router.push({path: '/login'});
          }
        }
      }, error => {
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(error.message), '图片上传出错!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
        console.log("异步调用失败返回:" + JSON.stringify(res));
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(res.message), '图片上传出错', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });
    },
    img_cancel() {
      this.img_dialog = false;
    },
    //查询图片
    img_select() {
      let that = this;
      let _jsondata_ = {
        perPageSize: 100,
        currentPage: 1,
        obj_bank_center_id: that.customGlobal.userInfoExt.cui_bank_center_id,
        obj_province_id: that.customGlobal.userInfoExt.cui_province_id,
        obj_city_id: that.customGlobal.userInfoExt.cui_city_id,
        obj_county_id: that.customGlobal.userInfoExt.cui_county_id,
        obj_station_id: that.customGlobal.userInfoExt.cui_station_id,
        bdbi_type: '1',
        bdbi_status: '1',
        bdbi_device_id: that.ruleForm.di_uuid,
        bdbi_qr_type:''
      };
      const loading = this.$loading({
        lock: true,
        text: '提交中...',
        target: '.main',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0)'
      });
      var a1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.Advertisement_select,
        //this.customGlobal.serverUrl.baseURI,
        this.customGlobal.serverUrl.orgBaseURI,
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
      );
      a1.then((res) => {
        if (res.data.status === 'success') {
          loading.close();
          that.img_tableData = [];
          that.img_tableData = res.data.rows;
        } else {
          console.info("商品查询失败");
          loading.close();
          if (res.data.msg === '请登录认证') {
            this.$router.push({path: '/login'});
          }
        }
      }, error => {
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(error.message), '商品查询失败!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
        console.log("异步调用失败返回:" + JSON.stringify(res));
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(res.message), '商品查询失败', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });
    },
    //删除图片
    delete_img(item) {
      let that = this;
      that.bdbi_id = item.bdbi_id;
      console.log(that.bdbi_id);
      this.$confirm('是否删除图片', '删除提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        dangerouslyUseHTMLString: true,
        closeOnClickModal: false,
        closeOnPressEscape: false,
      }).then(() => {
        let _jsondata_ = {
          bdbi_id: that.bdbi_id,
          bdbi_cui_id: that.customGlobal.userInfoExt.cui_uuid,
        };
        const loading = this.$loading({
          lock: true,
          text: '提交中...',
          target: '.main',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0)'
        });
        var s3 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.Advertisement_delete,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          {"Content-Type": "application/x-www-form-urlencoded"},
          this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
        );
        s3.then((res) => {
          if (res.data.status == 'success') {
            loading.close();
            this.$message({
              type: 'success',
              showClose: true,
              duration: 3000,
              dangerouslyUseHTMLString: true,
              message: '<span style="color:black;">图片删除成功</span>'
            });
            that.img_select();
          } else {
            console.info("图片删除失败");
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({path: '/login'});
            }
          }
        }, error => {
          loading.close();
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(error.message), '图片删除出错!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(res.message), '图片删除出错', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消操作'
        });
      });
    }
  }
}
</script>

